.user-lists{
    width: 50%;
    min-width: 500px;
    margin: 100px auto;
    border: 1px solid #ccc;
    border-radius: 10px;
    overflow-y: auto;
    height: 50vh;
    max-height: 500px;
}
.user-list{
    display: flex;
    align-items: center;
    padding: 0.75rem;
    border-bottom: 1px solid #ccc;
    position: relative;
}
.online{
    position: absolute;
    right: 15px;
}
.user-list>img{
    border-radius: 50%;
}
.user-list>.username{
    margin: 0 0.5rem;
    text-transform: uppercase;

}
.user-list>.online.on{
    color: rgb(246, 136, 194);
    font-weight: 500;
}
.user-list>.online.off{
    color: rgba(246, 136, 194,.5);
    font-weight: 500;
}